.action-box {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-self: center;
  text-align: center;
  list-style-type: none;
  margin: 0;
  padding: 0;
  .logo {
    width: 64px;
    height: 32px;
    margin: 6px 18px 0 0;
    background-image: url("https://stc-id.nixcdn.com/v11/images/header_new/logo_new.png");
    background-size: 100%;
    margin-bottom: 5px;
  }
  .more {
    content: "";
    background: url("https://stc-id.nixcdn.com/v11/images/header_new/ic_more_new.png");
    width: 21px;
    height: 4px;
  }
  li {
    color: #333;
    font-weight: 400;
    font-family: "NhacCuaTui", sans-serif;
    font-style: normal;
    align-self: center;
    padding: 1px 16px 0 0;
  }
  li:hover {
    cursor: pointer;
    color: rgb(0, 217, 255);
  }
  .user-login {
    height: 45px;
    position: relative;
    padding-bottom: 20px;
    margin-left: 40px;
    .user-login-wrapper {
      width: 110px;
      height: 40px;
      margin-right: 10px;
      position: relative;
      .user-login-avt {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 2;
        width: 40px;
        .avt {
          width: 40px;
          height: 40px;
          border-radius: 24px;
          border: 2px solid #fff;
          background-color: #fff;
        }
      }
      .vip {
        position: absolute;
        z-index: 1;
        right: 0;
        top: 5px;
        background-image: linear-gradient(90deg, #f2af12 0%, #ffd200 100%);
        height: 28px;
        border-radius: 0 34px 34px 0;
        .txt-vip {
          font-size: 14px;
          font-weight: bold;
          color: #fff;
          text-align: right;
          padding: 4px 10px 4px 12px;
        }
      }
    }
  }
  .function {
    width: auto;
    height: 42px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    .function-listen {
      width: 30px;
      height: 22px;
      padding-bottom: 20px;
      margin-right: 25px;
      margin-bottom: 5px;
    }
    .function-upload {
      width: 30px;
      height: 22px;
      padding-bottom: 20px;
      margin-bottom: 5px;
      margin-right: 15px;
    }
  }
  .search {
    width: 200px;
    height: 32px;
    background-color: rgba(245, 245, 245, 1);
    margin-top: 6px;
    border-radius: 16px;
    font-size: 12px;
    font-family: "NhacCuaTui", sans-serif;
    .form-search {
      width: 100%;
      .glass-search-icon {
        content: "";
        margin: 0;
        background: url("https://stc-id.nixcdn.com/v11/images/ic_search.png") no-repeat 11px;
        background-size: 16px;
        width: 32px;
        height: 32px;
        cursor: pointer;
        border-radius: 16px 0 0 16px;
        display: inline-block;
        -webkit-font-smoothing: antialiased;
        border: none;
        outline: none;
      }
      input {
        width: 100%;
        outline: none;
        border: 0;
        box-sizing: none;
      }
      :focus {
        outline: none;
        border: 0;
        box-sizing: none;
      }
      :hover {
        color: black;
      }
    }
  }
}
