.small-box-absolute {
  position: relative;
  width: 100%;
  height: 109px;
  .box {
    cursor: pointer;
    .overlay {
      position: absolute;
      z-index: 2;
      width: 100%;
      height: 100%;
    }
    &:hover .overlay {
      background: rgba(0, 0, 0, 0.3);
    }
    .view-mv {
      position: absolute;
      right: 0;
      z-index: 101;
      display: flex;
      flex-direction: row;
      padding: 0 5px 0 15px;
      font-size: 12px;
      line-height: 24px;
      color: #c7c7c7;
      background: url("https://stc-id.nixcdn.com/v11/images/bg_view_listen.png") left top;
      cursor: pointer;
      .icon-view {
        width: 14px;
        height: 14px;
        margin: 8px 3px 0 0;
        background: url("https://stc-id.nixcdn.com/v11/images/icon-repeat.png") 0 -237px no-repeat;
      }
    }
    .bg-video {
      cursor: pointer;
      .image {
        width: 195px;
        height: 109px;
        margin-bottom: 5px;
      }
      .icon-play {
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 1001;
        display: block !important;
        width: 48px;
        height: 48px;
        margin: -24px 0 0 -24px;
        background: url("https://stc-id.nixcdn.com/v11/images/icon.png") left -28px no-repeat;
        opacity: 0;
        visibility: visible;
      }
    }
  }
  &:hover .bg-video .icon-play {
    opacity: 1;
    transition: opacity 0.5s linear;
  }
  .view-time {
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 100;
    padding: 4px 6px;
    font-size: 11px;
    font-weight: bold;
    line-height: 16px;
    color: #fff;
    background: transparent url("https://stc-id.nixcdn.com/v11/images/transparent.png") repeat;
  }
}
